<template>
    <div class="fater-body-show">
        <el-card shadow="never">
			<div slot="header">
				<strong></strong>
			</div>
			<div>
				<el-form :inline="true" :model="qryForm">
					<el-form-item >
						<el-input v-model="qryForm.name"
							placeholder=""
							autocomplete="off"></el-input>
					</el-form-item>
					<el-form-item >
						<el-input v-model="qryForm.card"
							placeholder=""
							autocomplete="off"></el-input>
					</el-form-item>
					<el-form-item >
						<el-input v-model="qryForm.address"
							placeholder=""
							autocomplete="off"></el-input>
					</el-form-item>
					<el-form-item>
						<el-button type="primary"
							icon="el-icon-search" @click="getPageLikeInfo()"></el-button>
					</el-form-item>
				</el-form>
			</div>
		</el-card>

        <el-card shadow="never">
			<div slot="header">
				<el-button v-if="userType == 0" icon="el-icon-plus" disabled
					type="primary" size="mini" @click="showAddWin()"></el-button>
				<el-button v-if="userType == 1" icon="el-icon-edit"
					type="primary" size="mini" @click="showAddWin()"></el-button>
			</div>
			<div>
				<el-table v-loading="loading" 
					element-loading-text="" element-loading-spinner="el-icon-loading"
					element-loading-background="rgba(124, 124, 124, 0.8)" :data="pageInfos" border>
						<el-table-column align="center" type="index"></el-table-column>
						<el-table-column align="center" prop="name" label=""></el-table-column>
						<el-table-column align="center" prop="card" label=""></el-table-column>
						<el-table-column align="center" prop="phone" label=""></el-table-column>
						<el-table-column align="center" prop="address" label=""></el-table-column>
						<el-table-column align="center" prop="vaccinateTime" label=""></el-table-column>
						<el-table-column align="center" prop="vaccinateNo" label=""></el-table-column>
						<el-table-column align="center" prop="uName" label=""></el-table-column>
						<el-table-column align="center" label="">
							<template slot-scope="scope">
								<el-button v-if="userType == 0" icon="el-icon-edit" disabled
										type="primary" size="mini" @click="showUpdWin(scope.row)"></el-button>
								<el-button v-if="userType == 0" icon="el-icon-delete" disabled
									type="danger" size="mini" @click="delVaccinateInfo(scope.row.id)"></el-button>
								<el-button v-if="userType == 1" icon="el-icon-edit"
										type="primary" size="mini" @click="showUpdWin(scope.row)"></el-button>
								<el-button v-if="userType == 1" icon="el-icon-delete"
									type="danger" size="mini" @click="delVaccinateInfo(scope.row.id)"></el-button>
							</template>
						</el-table-column>
				</el-table>
				<el-pagination v-if="pageTotal > 1" style="margin-top: 15px;" @size-change="handleSizeChange"
					@current-change="handleCurrentChange" :current-page="pageIndex" :page-sizes="[10, 20, 50]"
					:page-size="pageSize" layout="total, sizes, prev, pager, next, jumper" :total="totalInfo">
				</el-pagination>
			</div>
        </el-card>

        <el-dialog title="" width="800px" :visible.sync="showAddFlag">
			<el-form label-width="90px" :model="vaccinateForm">
				<el-row :gutter="15">
					<el-col :span="12">
						<el-form-item label="">
							<el-input v-model="vaccinateForm.name" 
								placeholder="" autocomplete="off"></el-input>
						</el-form-item>
					</el-col>
					<el-col :span="12">
						<el-form-item label="">
							<el-input v-model="vaccinateForm.card" 
								placeholder="" autocomplete="off"></el-input>
						</el-form-item>
					</el-col>
				</el-row>
				<el-row :gutter="15">
					<el-col :span="12">
						<el-form-item label="">
							<el-select style="width:100%"
								v-model="vaccinateForm.vaccinateNo" placeholder="">
								<el-option label="" value=""></el-option>
								<el-option label="" value=""></el-option>
								<el-option label="" value=""></el-option>
							</el-select>
						</el-form-item>
					</el-col>
					<el-col :span="12">
						<el-form-item label="">
							<el-date-picker style="width:100%"
								v-model="vaccinateForm.vaccinateTime" type="date"
								value-format="yyyy-MM-dd"></el-date-picker>
						</el-form-item>
					</el-col>
				</el-row>
				<el-row :gutter="15">
					<el-col :span="12">
						<el-form-item label="">
							<el-input v-model="vaccinateForm.phone" 
								placeholder="" autocomplete="off"></el-input>
						</el-form-item>
					</el-col>
					<el-col :span="12">
						<el-form-item label="">
							<el-input  v-model="vaccinateForm.address" 
								placeholder="" autocomplete="off"></el-input>
						</el-form-item>
					</el-col>
				</el-row>
			</el-form>
			<div slot="footer" class="dialog-footer">
				<el-button @click="showAddFlag = false"> </el-button>
				<el-button type="primary" @click="addVaccinateInfo()"> </el-button>
			</div>
		</el-dialog>

        <el-dialog title="" width="600px" :visible.sync="showUpdFlag">
			<el-form label-width="90px" :model="vaccinateForm">
				<el-row :gutter="15">
					<el-col :span="12">
						<el-form-item label="">
							<el-input v-model="vaccinateForm.name" 
								placeholder="" autocomplete="off"></el-input>
						</el-form-item>
					</el-col>
					<el-col :span="12">
						<el-form-item label="">
							<el-input v-model="vaccinateForm.card" 
								placeholder="" autocomplete="off"></el-input>
						</el-form-item>
					</el-col>
				</el-row>
				<el-row :gutter="15">
					<el-col :span="12">
						<el-form-item label="">
							<el-select style="width:100%"
								v-model="vaccinateForm.vaccinateNo" placeholder="">
								<el-option label="" value=""></el-option>
								<el-option label="" value=""></el-option>
								<el-option label="" value=""></el-option>
							</el-select>
						</el-form-item>
					</el-col>
					<el-col :span="12">
						<el-form-item label="">
							<el-date-picker style="width:100%"
								v-model="vaccinateForm.vaccinateTime" type="date"
								value-format="yyyy-MM-dd"></el-date-picker>
						</el-form-item>
					</el-col>
				</el-row>
				<el-row :gutter="15">
					<el-col :span="12">
						<el-form-item label="">
							<el-input v-model="vaccinateForm.phone" 
								placeholder="" autocomplete="off"></el-input>
						</el-form-item>
					</el-col>
					<el-col :span="12">
						<el-form-item label="">
							<el-input  v-model="vaccinateForm.address" 
								placeholder="" autocomplete="off"></el-input>
						</el-form-item>
					</el-col>
				</el-row>
			</el-form>
			<div slot="footer" class="dialog-footer">
				<el-button @click="showUpdFlag = false"> </el-button>
				<el-button type="primary" @click="updVaccinateInfo()"> </el-button>
			</div>
		</el-dialog>
    </div>
</template>

<style>

</style>

<script>

	import { 
		getPageVaccinates,
		addVaccinates,
		updVaccinates,
		delVaccinates 
	} from '../../api/index.js'

    import {getLoginUser} from "../../api";
	import store from "../../store";

    export default{

        data(){

            return {
                pageInfos: [],
				pageIndex: 1,
				pageSize: 10,
				pageTotal: 0,
				totalInfo: 0,
				loading: true,
				showAddFlag: false,
				showUpdFlag: false,
				vaccinateForm: {
					id: "",
					name: "",
					card: "",
					phone: "",
					address: "",
					vaccinateNo: "",
					vaccinateTime: "",
					token: this.$store.state.token
				},
                qryForm: {
					name: "",
					card: "",
					phone: "",
					address: "",
				},
            }
        },
        methods: {
			getUserType(store) {
				
				let token = null;

				if(store.state.token){
					token = store.state.token;
				}else{

				token = sessionStorage.getItem("token");
					store.state.token = sessionStorage.getItem("token");
				}

				getLoginUser(token).then(resp =>{
					this.userType = !resp.data.type;
				});
				console.log(this.userType);
			},

			getPageInfo(pageIndex, pageSize) {

				getPageVaccinates(pageIndex, pageSize, this.$store.state.token, this.qryForm.name, this.qryForm.card, 
							this.qryForm.phone, this.qryForm.address).then(resp => {

					this.pageInfos = resp.data.data;
					this.pageIndex = resp.data.pageIndex;
					this.pageSize = resp.data.pageSize;
					this.pageTotal = resp.data.pageTotal;
					this.totalInfo = resp.data.count;

					this.loading = false;
				});
			},
            getPageLikeInfo() {
				getPageVaccinates(1, this.pageSize, this.$store.state.token, this.qryForm.name, this.qryForm.card, 
							this.qryForm.phone, this.qryForm.address).then(resp => {

					this.pageInfos = resp.data.data;
					this.pageIndex = resp.data.pageIndex;
					this.pageSize = resp.data.pageSize;
					this.totalInfo = resp.data.count;
					this.pageTotal = resp.data.pageTotal;
					this.loading = false;
				});
			},
			handleSizeChange(pageSize) {

				this.getPageInfo(this.pageIndex, pageSize, this.$store.state.token, this.qryForm.name, this.qryForm.card, 
							this.qryForm.phone, this.qryForm.address);
			},
			handleCurrentChange(pageIndex) {

				this.getPageInfo(pageIndex, this.pageSize, this.$store.state.token, this.qryForm.name, this.qryForm.card, 
							this.qryForm.phone, this.qryForm.address);
			},
			initVaccinateForm() {

				this.vaccinateForm = {
					id: "",
					name: "",
					card: "",
					phone: "",
					address: "",
					vaccinateNo: "",
					vaccinateTime: "",
					token: this.$store.state.token
				}
			},
            showAddWin() {

				this.initVaccinateForm();
				this.showAddFlag = true;
			},
			showUpdWin(row) {

				this.vaccinateForm = row;
				
				this.showUpdFlag = true;
			},
			addVaccinateInfo(){

				addVaccinates(this.vaccinateForm).then(resp =>{

					this.$message({
						message: resp.msg,
						type: 'success'
					});

					this.getPageInfo(1, this.pageSize);

					this.showAddFlag = false;
				});
			},
			updVaccinateInfo(){

				updVaccinates(this.vaccinateForm).then(resp =>{

					this.$message({
						message: resp.msg,
						type: 'success'
					});

					this.getPageInfo(1, this.pageSize);

					this.showUpdFlag = false;
				});
			},
			delVaccinateInfo(id){

				this.$confirm(', ?', '', {
					confirmButtonText: '',
					cancelButtonText: '',
					type: 'warning'
				}).then(() => {
					
					delVaccinates(id).then(resp =>{
					
						this.$message({
							message: resp.msg,
							type: 'success'
						});

						this.getPageInfo(1, this.pageSize);
					});	
				});			
			}
        },
        mounted(){
			this.getUserType(store);
			this.getPageInfo(1, this.pageSize);
        }
    }
</script>
